@import url(https://fonts.googleapis.com/css?family=Raleway:400,500);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);
html, body {
    background-color: rgb(255, 255, 255);
    margin: 0;
    height: 100%;
    width: 100%;
    overflow: hidden;
}


/*MENU CONTAINER*/


#menu{
    margin: 0;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    width: 100%;
}

.scroll-menu-arrow {
    padding:5px;
    cursor: pointer;
}


/*CARDS*/

.card {
    margin-left: 2vw;
    margin-right: 2vw;
}

.front {
    border-radius: 10px;
    width: 70vh;
    height: 84vh;    
    cursor: pointer;
}

.back{
    background-color: rgb(233, 253, 144);
    border-radius: 10px;
    width: 70vh;
    height: 84vh;  
    overflow: hidden;  
}

.quoteBox {
    text-align: left;
    font-size: 4vh;
    font-family: 'Raleway', sans-serif;
    margin: 6vh;
}

.quote {
    text-align: inherit;
    height: 62vh;
    width: 100%;
    overflow: auto;
}

.author {
    margin-right: 3vh;
    text-align: end;
}

.buttons{
    display: flex;
    position: fixed;
    bottom: 6%;
    left: 5%;
    right: 5%;
    justify-content: space-around;
    
}

.button {
    text-decoration: none;
	border: none;
    border-radius: 3px;
	color: #fff;
    background-color:black;
	outline: none;
	font-size: 4vh;
    padding: 1vh 2vh 1vh 2vh;
    margin: 1px;
	opacity: 1;
    cursor: pointer;
}

.newQuote {
    font-family: 'Raleway', sans-serif;
}

@media (max-aspect-ratio: 11/12){
    .front {
        width: 75vw;
        height: 90vw;      
    }
    
    .back{
        width: 75vw;
        height: 90vw;        
    }

    .quoteBox {
        margin: 6vw;
    }
    
    .quote {
        text-align: inherit;
        height: 63vw;
        width: 100%;
        overflow: auto;
    }

    .button {
        font-size: 3vw;
        padding: 2vw 3vw 2vw 3vw;
    }
}

.loading-container {
	min-height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
	/* background-color: white; */
	overflow: hidden;
	-webkit-transition: all 0.3s 0.6s ease-out;
	transition: all 0.3s 0.6s ease-out;
}

.loader-dzg {
	-webkit-filter: saturate(3);
	        filter: saturate(3);
	width: .1px;
	height: .1px;
	border: 40px solid transparent;
	border-radius: 5px;
	-webkit-animation: loader 3s ease-in infinite, spin 1s linear infinite;
	        animation: loader 3s ease-in infinite, spin 1s linear infinite
}

.loader-dzg::before {
	-webkit-filter: saturate(.3);
	        filter: saturate(.3);
	display: block;
	position: absolute;
	z-index: -1;
	margin-left: -40px;
	margin-top: -40px;
	content: '';
	height: .1;
	width: .1;
	border: 40px solid transparent;
	border-radius: 5px;
	animation: loader 2s ease-in infinite reverse, spin .8s linear infinite reverse
}

.loader-dzg::after {
	display: block;
	position: absolute;
	z-index: 2;
	margin-left: -10px;
	margin-top: -10px;
	content: '';
	height: 20px;
	width: 20px;
	border-radius: 20px;
	background-color: #fff
}

@-webkit-keyframes loader {
	0% {
		border-bottom-color: transparent;
		border-top-color: #114357
	}
	25% {
		border-left-color: transparent;
		border-right-color: #826c75
	}
	50% {
		border-top-color: transparent;
		border-bottom-color: #f29492
	}
	75% {
		border-right-color: transparent;
		border-left-color: #826c75
	}
	100% {
		border-bottom-color: transparent;
		border-top-color: #114357
	}
}

@keyframes loader {
	0% {
		border-bottom-color: transparent;
		border-top-color: #114357
	}
	25% {
		border-left-color: transparent;
		border-right-color: #826c75
	}
	50% {
		border-top-color: transparent;
		border-bottom-color: #f29492
	}
	75% {
		border-right-color: transparent;
		border-left-color: #826c75
	}
	100% {
		border-bottom-color: transparent;
		border-top-color: #114357
	}
}

@-webkit-keyframes spin {
	0% {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg)
	}
	100% {
		-webkit-transform: rotate(-360deg);
		        transform: rotate(-360deg)
	}
}

@keyframes spin {
	0% {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg)
	}
	100% {
		-webkit-transform: rotate(-360deg);
		        transform: rotate(-360deg)
	}
}
